import createLogger from 'redux-logger';

/**
 *  logger 
 * @param store
 */
let logger = store => next => action => {
  return next(action);
};

// : https://github.com/evgenyrodionov/redux-logger#options
logger = createLogger({
  // ...options
});

export default logger;




